import { Component, OnInit } from '@angular/core';
import {ModalHelper} from "../../helper/modal.helper";
import {SettingsStore} from "../../store/settings.store";
import {SettingsHelper} from "../../helper/settings.helper";
import * as Parse from 'parse';
import {UserStore} from "../../store/user.store";
import {IUser} from "../../model/user.model";
import {NewsService} from "../../service/news.service";
import {NewsStore} from "../../store/news.store";
import {Router} from "@angular/router";
import {INews} from "../../model/news.model";
import {LessonService} from "../../service/lesson.service";
import {ILesson} from "../../model/lesson.model";

@Component({
  selector: 'app-home',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
})
export class HomePage implements OnInit {
  user: Parse.Object = Parse.User.current();

  sliderOptions = {
    initialSlide: 0,
    slidesPerView: 1.2,
    spaceBetween: 2.5
  };


  constructor(private lessonService: LessonService, private newsService: NewsService, public newsStore: NewsStore, public userStore: UserStore, public modalHelper: ModalHelper, private settingsStore: SettingsStore,private settingsHelper: SettingsHelper, private router: Router) {
  }

  async ngOnInit() {
    this.userStore.setUser(this.user.toJSON() as unknown as IUser);

    await this.settingsHelper.loadSettings();
    await this.newsService.getAllNews();
    await this.lessonService.getAllLessons();

  }

  selectNews(news: INews) {
    this.newsStore.setSelectedNews(news);
    this.router.navigate(['/news']);
  }

}
